/* Site Foundation

	https://themes.getbootstrap.com/products/marketing

*/


@import url('https://fonts.googleapis.com/css?family=Work+Sans:300,400,500,600');

@import "variables",
"mixins",
"partials/normalize",
"partials/grid",
"partials/buttons",
"partials/banner",
"partials/header",
"partials/footer",
"partials/feature";


/* Start */
* {
	box-sizing: border-box;
}

html {
	font-size: 62.5%;
	height: 100%;
}

body {
	background-color: $bkg_body;
	padding: 0;
	margin: 0;
	text-rendering: optimizeLegibility;
	width: 100%;
	height: 100%;
	-webkit-text-size-adjust: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

img {
	max-width: 100%;
	width: auto;
	height: auto;
}

a {
	color: $link;
	text-decoration: none;
	transition: $transition;

	&:hover {
		color: $link_hover;
		text-decoration: none;
	}

	&:active {
		transition: all 0.05s linear;
	}
}

.offscreen {
	position: absolute;
	left: -9999px;
	opacity: 0;
}

.skip-link {
	position: absolute;
	top: -100px;
	/* Styled to match the default screen reader text */
	background-color: #f1f1f1;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, .6);
	color: #21759b;
	display: block;
	font-size: 14px;
	font-weight: 700;
	height: auto;
	left: 6px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	width: auto;
	z-index: 100000;
	transition: top .75s ease-out;

	&:focus {
		color: #21759b;
		top: 7px;
		transition: top 0s;
	}
}

p,
ul li,
ol li,
input,
textarea,
table td,
body {
	font-size: 1.8rem;
	line-height: 1.6;
	letter-spacing: -0.02em;
	font-weight: 300;
	font-family: $font;
	list-style: none;
	color: $color_text;
	-webkit-appearance: none;
}

p {
	&.small {
		font-size: 1.6rem;
	}
}

strong {
	font-weight: 600;
}

p,
ul,
ol {
	margin: 0 0 1.5rem;
}

p+p.button {
	margin-top: 5px;
}

ul,
ol,
li {
	padding: 0;
	list-style: none;
}

ul {
	padding: 5px;
	list-style: none;

	>li {
		margin: 0 0 10px;
		padding: 0 0 0 26px;
		list-style: none;
		background: url('../img/ico-bullet.png') no-repeat 6px 0.45em;
	}

	ul {
		margin: 0;

		li {
			margin: 7px 0;
		}
	}
}

ol {
	margin-left: 8px;
	padding: 5px;
	counter-reset: item;

	>li {
		list-style: none;
		margin: 0 0 10px 0;
		padding: 0 0 0 20px;
	}
}

/* Heading Styles */
h1,
h2,
h3,
h4,
h5,
h6 {
	color: $color_header;
	font-family: $font;
	padding: 0;
	margin: 0 0 1.5rem;
	line-height: 1.15em;
	text-transform: none;
	font-weight: 400;
}

h1 {
	font-family: $heading;
	font-size: 3.4rem;
	margin: 0 0 20px;
	line-height: 1.3em;
}

h2 {
	font-family: $heading;
	font-size: 3rem;
	line-height: 1.25em;
}

h3 {
	font-size: 2.6rem;
	line-height: 1.3em;
}

h4 {
	font-size: 1.8rem;
	line-height: 1.3em;
	margin: 0 0 5px;
}

h5 {
	letter-spacing: 0.15em;
	font-size: 1.4rem;
	line-height: 1.3em;
	margin: 0 0 5px;
	font-weight: 600;
	color: $color_text;
	text-transform: uppercase;
	opacity: 0.65;
}

h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover {
	text-decoration: underline;
}


/* Misc */
.bkg-cover {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;

	&.btm {
		background-position: center bottom;
	}
}

.bkg-contain {
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center center;

	&.btm {
		background-position: center bottom;
	}
}

.spacer {
	height: 30px;
	clear: both;
	display: block;

	&.large {
		height: 50px;
	}

	&.small {
		height: 15px;
	}
}

.hand {
	cursor: help;
}

.clearfix:after {
	clear: both;
	content: ".";
	display: block;
	height: 0;
	visibility: hidden;
}

.clear {
	clear: both;
	height: auto;
	overflow: hidden;
	width: 100%;
	padding: 0;
	margin: 0;
}

.disabled {
	cursor: default !important;
}

.padding-top {
	padding-top: 20px;
}

.no-padding {
	padding: 0 !important;
}

.no-padding-top {
	padding-top: 0 !important;
}

.no-padding-btm {
	padding-bottom: 0 !important;
}

.no-margin-btm {
	margin-bottom: 0 !important;
}

hr {
	height: 1;
	overflow: hidden;
	border: none;
	border-top: 1px solid #efefef;
	display: block;
	width: 100%;
	clear: both;
	margin: 30px 0;
	background: none;
}

.hide {
	display: none;
}

.centered {
	text-align: center;
	margin: 0 auto;
}

.no-margin {
	margin-bottom: 0;
}

.regular {
	font-weight: 400;
}

.text-right,
.text-right * {
	text-align: right;
}

table {
	overflow: hidden;
	width: 100%;
	margin: 0;
	padding: 0;
	border-collapse: collapse;
	table-layout: fixed;
	display: table;

	tr {
		background: #eef0f3;

		&:nth-child(odd) {
			background: #dde3e7;
		}
	}

	td {
		padding: 10px;
		width: 25% !important;
		border: 1px solid #fff;
		border-right: none;
		border-top: none;
		word-wrap: break-word;
	}
}

.span-404 h2 {
	font-size: 600px !important;
	margin-top: -260px;
}

hr.center {
	margin: 30px auto 15px;
}

hr.full-width {
	margin: 30px auto 15px;
	width: 100%;
}

hr.dashed {
	border-style: dashed;
}

.positioned {
	position: absolute;
	z-index: 2;
}

.screen-reader-text {
	display: none;
}

.bypostauthor {}

.sticky {}

code {
	display: block;
	clear: both;
	unicode-bidi: embed;
	font-family: monospace;
	white-space: pre-wrap;
	word-break: break-word;
	text-align: left;
	width: 100%;
	font-size: 13px;
	font-size: 1.3rem;
	color: #444;
	font-weight: 400;
	background: #efefef;
	border: 1px solid #ccc;
	padding: 15px;
	line-height: 1.4em;
	margin: 0 0 15px;
	text-shadow: 1px 1px rgba(255, 255, 255, 0.5);
}

/* Alerts */
.alert,
.moderation {
	font-weight: 400;
	font-size: 1.4rem;
	padding: 5px 7px;
	background: #ffffcc;
	margin: 0 0 10px;
	display: inline-block;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;
	border: 1px solid #e9e4c1;
	color: #222;
}

span.required {
	color: #e02e2e;
}

/* Responsive Videos */
iframe {
	margin: 0 0 20px;
}

#video-wrapper {
	width: 100%;
	display: none;
}

.video-container {
	position: relative;
	padding-bottom: 56.25%;
	padding-top: 30px;
	height: 0;
	overflow: hidden;
	margin-bottom: 30px;
	margin-top: 20px;
	-webkit-transform: inherit;
	-moz-transform: inherit;
	transform: inherit;

	iframe,
	object,
	embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		margin: 0;
	}
}
